* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
		Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	background-color: #f1f1f1;
	color: #333;
}
.container {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	min-height: 100vh;
	padding: 0 20px;
}

.container h1 {
	text-transform: uppercase;
	margin-bottom: 30px;
	text-align: center;
}

.countdown {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-around;
	text-align: center;
}

.countdown p {
	padding: 20px;
	background-color: #fff;
	border-radius: 5px;
	width: 100px;
	box-shadow: 0 5px 5px rgb(0, 0, 0, 0.3);
	margin-bottom: 20px;
}

.countdown article {
	margin: 10px;
}

.countdown h2 {
	font-size: 14px;
	text-transform: uppercase;
}
